.App {
  text-align: center;
  position: absolute;
  width: 80vw;
  height: 70vw;
  max-width: 508px;
  max-height: 508px;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 50%;
  margin: 0 auto;
}

.action {
  margin-bottom: 19px;
  width: 100%;
  padding: 12px 0 8px;
  background: #FFFFFF;
  border: 1px solid #F34293;
  color: #F34293;
  cursor: pointer;
  transition: all 0.2s ease-in;
}

.count-outer {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.count-inner {
  display: flex;
  margin-right: 5px;
}

.unit {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #0275B4;
  padding: 20px 10px;
}

.unit--desc {
  margin-top: 8px;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: #706C6D;
}

.unit:first-child {
  margin-right: 5px;
}

.countdown{
  margin: 0 auto;
  font-size: 30px
}

.countdown-timer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: white;
}

.action:hover {
  background-color: #F34293;
  color: #FFFFFF;
}

.timer {
  font-size: 28px;
  display: flex;
  line-height: 34px;
  text-align: center;
  color: #706C6D;
}

.order {
  width: 264px;
  margin-top: 15px;
  height: 32px;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: #F34293;
}

.pink {
  position: relative;
  padding-left: 8px;
  padding-bottom: 15px;
  border-bottom: 2px solid #0275B4;
}

.pink1 {
  position: absolute;
  margin: 0 auto ;
  background-color: #F34293;
  min-width: 265px;
  height: 43px;
  bottom: 8px;
  right: -6px;
  z-index: -1;
}

.pink2 {
  background-color: #F34293;
  height: 43px;
  margin: 0 auto;
  min-width: 265px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 30px;
  line-height: 36px;
  text-align: center;
  
  color: #FFFFFF;
}

.label {
  background-color: #F34293;
  height: 43px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  position: relative;
  font-size: 30px;
  line-height: 36px;
  text-align: center;
  color: #FFFFFF;
  box-shadow: 6px 8px 0 0 #f34293;
}

.label::before {
  content: '';
  width: 100%;
  height: 2px;
  display: block;
  position: absolute;
  background-color: #0275B4;
  left: 6px;
  bottom: -14px;
}

.heading2 {
  font-weight: 600;
  font-size: 42px;
  line-height: 50px;
  margin: 0 auto;
  color: #0275B4;
}

.bargain{
  color: white;
  font-size: 30px;
}

.buy {
  color: #706C6D;
  margin: 15px auto;
  font-size: 28px;
  line-height: 34px;
  text-align: center;
}

.wait {
  margin: 15px auto;
  font-size: 28px;
  line-height: 34px;
  text-align: center;
  color: #706C6D;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #0275B4;
  position: relative;
  border-radius: 5px;
  padding: 26px 22px 19px;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@media screen and (min-width: 480px) {
  .countdown-timer {
    flex-direction: row;
    align-items: center;
  }

  .count-outer {
    flex-direction: column;
  }

  .label {
    min-width: 265px;
    max-width: 360px;
  }

  .rectangle {
    width: 264px;
  }
}
